import { GraphicsCoreWrapper } from '..';

import { MouseEventData } from '../../AppCoreWrapper/IOWrapper/EventInteraction/MouseInteraction';

export class SceneWrapper {

    graphicsCoreWrapper: GraphicsCoreWrapper;

    constructor(graphicsCoreWrapper: GraphicsCoreWrapper) {
        this.graphicsCoreWrapper = graphicsCoreWrapper;

        this.graphicsCoreWrapper.rendererWrapper.renderer.sceneWrapper = this;
    }
    MouseEventController(e: MouseEventData) {
        this.graphicsCoreWrapper.model.app.implementation.MouseEventController(e);

    }
    Render(ctx: CanvasRenderingContext2D) {
        this.graphicsCoreWrapper.model.app.implementation.Render(ctx);
    }
    RemoveAllScene() {
        this.graphicsCoreWrapper.model.app.implementation.RemoveAllScene();
    }
    ShiftScene(v: string) {
        this.graphicsCoreWrapper.model.app.implementation.ShiftScene(v);
    }

    LoadDefinition(v0: string) {
        this.graphicsCoreWrapper.model.app.implementation.LoadDefinition(v0);
    }
}
