
import { Model } from '..';

import { CanvasWrapper } from './CanvasWrapper';
import { GeometryCoreWrapper } from './GeometryCoreWrapper';
import { RendererWrapper } from './RendererWrapper';
import { ThemeWrapper } from './ThemeWrapper';
import { SceneWrapper } from './SceneWrapper';

export class GraphicsCoreWrapper {
    model: Model;
    canvasWrapper: CanvasWrapper;
    geometryCoreWrapper: GeometryCoreWrapper;
    rendererWrapper: RendererWrapper;
    themeWrapper: ThemeWrapper;
    sceneWrapper: SceneWrapper;
    constructor(model: Model) {
        this.model = model;
        this.canvasWrapper = new CanvasWrapper(this);
        this.geometryCoreWrapper = new GeometryCoreWrapper(this);
        this.rendererWrapper = new RendererWrapper(this);
        this.themeWrapper = new ThemeWrapper(this);
        this.sceneWrapper = new SceneWrapper(this);
    }
}